.Gif {
    --gifShadow-size : var(--gridList-gap, 6px);
    margin-bottom: .5em;
    position: relative;
  }
  
  .Gif-buttons {
    position: absolute;
    top: 8px;
    right: 8px;
    z-index: 1;
  }
  
  .Gif-link {
    color: #000;
    display: block;
    text-decoration: none;
    position: relative;
    width: 100%;
  }
  
  .Gif h4 {
    background: rgba(0, 0, 0, .3);
    bottom: 0;
    font-size: 12px;
    color: #fff;
    margin: 0;
    position: absolute;
  }
  
  .Gif img {
    width: 100%;
    object-fit: contain;
    vertical-align: top;
  }
  
  i {
    font-size: 80px;
  }
  
  .Gif {
    margin-bottom: 0;
  }
  
  .Gif, .Gif-link {
    height: 100%;
  }
  .Gif-link img {
    height: inherit;
    object-fit: cover;
  }