@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,400;1,200&display=swap);
* {
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  color: blueviolet;
}

.App-content {

  background-color: var(--theme-body-bg);
  color: var(--theme-body-txt);
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  min-height: 100vh;
  padding: 16px;
  text-align: center;
}


.App-logo {
  margin: 10px auto;
  padding: 20px 0;
  width: 144px;
}

li{
  list-style: none;
}
* {
  text-decoration: none;
}

.Gif {
    --gifShadow-size : var(--gridList-gap, 6px);
    margin-bottom: .5em;
    position: relative;
  }
  
  .Gif-buttons {
    position: absolute;
    top: 8px;
    right: 8px;
    z-index: 1;
  }
  
  .Gif-link {
    color: #000;
    display: block;
    text-decoration: none;
    position: relative;
    width: 100%;
  }
  
  .Gif h4 {
    background: rgba(0, 0, 0, .3);
    bottom: 0;
    font-size: 12px;
    color: #fff;
    margin: 0;
    position: absolute;
  }
  
  .Gif img {
    width: 100%;
    object-fit: contain;
    vertical-align: top;
  }
  
  i {
    font-size: 80px;
  }
  
  .Gif {
    margin-bottom: 0;
  }
  
  .Gif, .Gif-link {
    height: 100%;
  }
  .Gif-link img {
    height: inherit;
    object-fit: cover;
  }
:root {
  --gridList-column: 250px;
  --gridList-rows: 250px;
  --gridList-gap: 6px;
}

.ListOfGifs {
  display: grid;
  min-height: 100vh;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-template-columns: repeat(auto-fit, minmax(var(--gridList-column), 1fr));
  grid-auto-rows: 250px;
  grid-auto-rows: var(--gridList-rows);
  grid-template-rows: masonry;
  grid-auto-flow: row dense;
  grid-gap: 6px;
  grid-gap: var(--gridList-gap, 6px);
  grid-gap: 6px;
  gap: 6px;
  grid-gap: var(--gridList-gap, 6px);
  gap: var(--gridList-gap, 6px);
  align-items: center;
}

@media screen and (min-width: 37.5rem) {
  :root {
    --gridList-column: 180px;
    --gridList-rows: 200px;
  }
}

@media screen and (min-width: 60rem) {
  :root {
    --gridList-column: 300px;
    --gridList-rows: 210px;
  }
}

.lds-ring {
	display: flex;
	align-items: center;
	height: 37px;
	padding-top: 15px;
	text-align: center;
	justify-content: center;
	margin: 0px auto;
}

.lds-ring div {
	display: inline-block;
	height: 10px;
	width: 10px;
	position: relative;
	box-shadow: rgba(0, 0, 0, 0.3) 0px 0px 20px;
	margin: 37px 10px 10px;
	-webkit-animation: 0.75s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s infinite
		alternate none running bouncer;
	        animation: 0.75s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s infinite
		alternate none running bouncer;
}

.lds-ring div:nth-child(5n + 1) {
	background-color: var(--brand-color_1);
}
.lds-ring div:nth-child(5n + 2) {
	-webkit-animation-delay: calc(0.1s);
	        animation-delay: calc(0.1s);
	background-color: var(--brand-color_2);
}
.lds-ring div:nth-child(5n + 3) {
	-webkit-animation-delay: calc(0.2s);
	        animation-delay: calc(0.2s);
	background-color: var(--brand-color_3);
}
.lds-ring div:nth-child(5n + 4) {
	-webkit-animation-delay: calc(0.2s);
	        animation-delay: calc(0.2s);
	background-color: var(--brand-color_4);
}
.lds-ring div:nth-child(5n + 5) {
	-webkit-animation-delay: calc(0.4s);
	        animation-delay: calc(0.4s);
	background-color: var(--brand-color_5);
}

@-webkit-keyframes bouncer {
	100% {
		-webkit-transform: scale(1.75) translateY(-20px);
		        transform: scale(1.75) translateY(-20px);
	}
}

@keyframes bouncer {
	100% {
		-webkit-transform: scale(1.75) translateY(-20px);
		        transform: scale(1.75) translateY(-20px);
	}
}
